import {useState, useEffect} from 'react'

function MyCountFunc() {
  const [count, setCount] = useState(0)

  useEffect(() => {
    const interVal = setInterval(() => {
      setCount(c => c + 1)
    }, 1000)
    return () => clearInterval(interVal)
  }, [])
  return <span>{count}</span>
}

export default MyCountFunc